import React, { Component } from 'react'
import { Table, Spin, Card, notification } from 'antd';
import { getAdminAllLogs, getAdminLogs } from "../../api/request"
const columns = [
    {
        title: '',
        dataIndex: 'opa',
    },
    {
        title: '',
        dataIndex: 'cTime',
    },
];
export default class OpaMyLogs extends Component {
    constructor() {
        super()
        this.state = {
            pagetotal: 10,
            data: [],
            sping: false
        }
    }
    componentDidMount() {
        //
        getAdminAllLogs({ name: sessionStorage.getItem("name") }).then(res => {
            if (res.data.state) {
                //
                this.setState({
                    pagetotal: res.data.data.length
                })
            } else {
                //,
                this.openNotificationWithIcon('error', ",")
            }
        }).catch(err => {
            //
            this.openNotificationWithIcon('error', "")
        })
        //(,,)
        this.getDatePage(1)
    }
    //
    openNotificationWithIcon = (type, msg) => {
        notification[type]({
            message: msg
        });
    };
    //()
    getDatePage(page) {
        //
        this.setState({
            sping: true
        })
        getAdminLogs({ name: sessionStorage.getItem("name"), page }).then(res => {
            if (res.data.state) {
                //
                this.setState({
                    data: res.data.data.map(item => {
                        return {
                            key: item._id,
                            admin: item.admin,
                            opa: item.opa,
                            cTime: item.cTime
                        }
                    })
                })
            } else {
                //,!
                this.openNotificationWithIcon('error', ",!")
            }
        }).catch(err => {
            //
            this.openNotificationWithIcon('error', "")
        }).finally(() => {
            //
            this.setState({
                sping: false
            })
        })
    }
    //
    changePage = (page) => {
        this.getDatePage(page)
    }
    render() {
        let { data, pagetotal, sping } = this.state
        return (
            <div>
                <Spin spinning={sping}>
                    <Card hoverable={true} title="" extra={<div><span>:</span><span style={{ color: "red",marginLeft:"10px",fontWeight:"900" }}>{sessionStorage.getItem("name")}</span></div>}>
                        <Table columns={columns} bordered dataSource={data} size="small" pagination={{ total: pagetotal, onChange: this.changePage }} />
                    </Card>
                </Spin>
            </div>
        )
    }
}
